							                  	<select class="custom-select form-control" id="resep_obat" name="resep_obat" required="" v-model="resepObatModel" @change="tambahResepKeRekamMedis(resepObatModel)">
			      									<option v-for="(row, index) in resep" :key="row.id" v-bind:value="index">{{ row.kode }} - {{ row.nama }}</option>
