												    </tr>
												  </thead>
												  <tbody>
												    <tr v-for="(row, index) in resepResult" :key="row.id">
												    	<td>{{ index+1 }}</td>
												    	<td >{{ row.kode }}</td>
												    	<td >{{ row.nama }}</td>
												    	<td >{{ row.stok }}</td>
												    	<td >{{ row.signa }}</td>
												    	<td><a class="btn btn-block btn-outline-danger btn-xs" @click="hapusDataResep(row.id)" v-if="row.hapus">Hapus</a></td>
												    </tr>
												  </tbody>
												</table>
					                  		</div>
					                  	</div>
					                  </div>
