		        </div>
			</div>
		</div>

		<!-- Modal -->
		<div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
		  <div class="modal-dialog" role="document">
		    <div class="modal-content">
		      <div class="modal-header">
		        <h5 class="modal-title" id="exampleModalLabel">Tambah Beautician</h5>
		        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
		          <span aria-hidden="true">&times;</span>
		        </button>
		      </div>
		      <div class="modal-body">
		        <div class="form-group">
	              	<div class="row">
	              		<div class="col-12 col-md-4">
	              			<label for="resep_obat">Beautician</label>
		                  	<select class="custom-select form-control" id="resep_obat" name="resep_obat" v-model="beauticianModel" @change="tambahbeauticianKeRekamMedis(beauticianModel)">
								<option v-for="(row, index) in beautician" :key="row.id" v-bind:value="index">{{ row.nama }}</option>
							</select>
	              		</div>
	              		<div class="col col-md-1"></div>
	              		<div class="col-12 col-md-12">
	              			<label class="mt-1">  </label>
	              			<table class="table table-bordered table-hover">
							  <thead>
							    <tr>
							      <th class="text-sm">NO</th>
							      <th scope="col" class="text-sm">NAMA</th>
							      <th scope="col" class="text-sm">ACTION</th>
							    </tr>
							  </thead>
							  <tbody>
							    <tr v-for="(row, index) in beauticianResult" :key="row.id">
							    	<td>{{ index + 1 }}</td>
							    	<td>{{ row.nama }}</td>
							    	<td><a class="btn btn-block btn-outline-danger btn-xs" @click="hapusDataBeautician(row.id)">Hapus</a></td>
							    </tr>
							  </tbody>
							</table>
	              		</div>
	              	</div>
	              </div>
		      </div>
		      <div class="modal-footer">
		        <button type="button" class="btn btn-secondary" data-dismiss="modal" @click="resetInputBeautician()">Keluar</button>
		      </div>
		    </div>
		  </div>
		</div>

	</div>
</template>

<script>
	import ContentHeader from '@/components/ContentHeader2'

	import { useRoute } from 'vue-router'
	import axios from 'axios'
	import { onMounted, ref, reactive, computed } from 'vue'
	import store from '@/store'
	import Swal from 'sweetalert2'
	import router from '@/router'
	import get_age  from '@/umur.js'

	export default{
		components: {
			ContentHeader: ContentHeader
		},
		setup(){
			const user = computed(() => store.getters['auth/user'])

			const route = useRoute()
			const kunjungan_id = route.params.id
			const nomor_rekam_medis = ref()
			// Data Pasien
			const pasien_id = ref()
			const nama_pasien = ref()
			const jenis_kelamin = ref()
			const jenis_kelamin_pada_ui = ref()
			const umur = ref()
			const loading = ref(false)

			// ---------Get data tindakan & resep obat---------
			const { resep, resepObatModel, resepResult, resep_id, nama_resep, tambahResepKeRekamMedis, hapusDataResep } = useResepObat()	

			const { tindakan, tindakanModel, tindakanResult, id_tindakan_ke_beautician, tindakan_id, nama_tindakan, tambahTindakanKeRekamMedis, hapusDataTindakan } = useTindakan()		

			const { icdx, icdxModel, icdxResult, kode_icdx, nama_icdx, tambahIcdxKeRekamMedis, hapusDataIcdx } = useIcdx()

			const { beautician, beauticianModel, tambahbeauticianKeRekamMedis, beauticianResult, hapusDataBeautician, resetInputBeautician, beautician_id, nama_beautician } = useBeautician(tindakanResult, id_tindakan_ke_beautician)

			const getTindakan = async () => {
				let response = await axios.get(`api/tindakan/get/${user.value.cabang_id}`)
				tindakan.value = response.data
			}

			const getResep = async () => {
				let response = await axios.get(`api/resep_obat/get/${user.value.cabang_id}`)
				resep.value = response.data
			}

			const getIcdx = async () => {
				let response = await axios.get(`api/icdx/get`);
				icdx.value = response.data
			}

			const getBeautician = () => {
				beautician.value.push({
					'id' : 0,
					'nama' : 'Jessica',
				})

				beautician.value.push({
					'id' : 1,
					'nama' : 'Niki',
				})
			}


	    	// ---------End Get data tindakan & resep obat---------

			const rekam_medis = reactive({
				'cabang_id' : user.value.cabang_id,
				'kunjungan_id' : kunjungan_id,
				'nomor_rekam_medis' : nomor_rekam_medis,
				'pasien_id' : 0,
				'dokter_id' : 0,
				'resep_id' : resep_id,
				'nama_resep': nama_resep,
				'tindakan_id' : tindakan_id,
				'nama_tindakan': nama_tindakan,
				'beautician_id' : beautician_id,
				'nama_beautician' : nama_beautician,
				'nama_pasien' : nama_pasien,
				'jenis_kelamin' : jenis_kelamin,
				'anamnesa' : 'anamnesa',
				'tinggi_badan': '33', 
				'berat_badan': '33', 
				'catatan_pemeriksaan_fisik': '33', 
				'frekuensi_denyut_jantung': '33', 
				'frekuensi_pernapasan': '33', 
				'suhu_tubuh': '33', 
				'tekanan_darah': '33', 
				'kode_icdx': '', 
				'nama_icdx': '',
				'diagnosa' : '', 
			})

			const getRekamMedis = async () => {
				let response = await axios.get(`api/kunjungan/getWhereId/${kunjungan_id}`)
				let dataPasien = await axios.get(`api/pasien/getWhereId/${response.data.pasien_id}`)

				
				rekam_medis.pasien_id = response.data.pasien_id
				rekam_medis.dokter_id = response.data.dokter_id

				nama_pasien.value = dataPasien.data.nama
				nomor_rekam_medis.value = dataPasien.data.nomor_rekam_medis
				jenis_kelamin.value = dataPasien.data.jenis_kelamin 
				
				if (dataPasien.data.jenis_kelamin == 'L') {
					jenis_kelamin_pada_ui.value = 'Laki-laki'
				} else {
					jenis_kelamin_pada_ui.value = 'Perempuan'
				}

				umur.value = dataPasien.data.tanggal_lahir
				umur.value = get_age(umur.value)
			}


			// Proses Rekam Medis
			const prosesRekamMedis = () => {
				loading.value = true

				rekam_medis.resep_id = resep_id.value
				rekam_medis.nama_resep = nama_resep.value

				rekam_medis.tindakan_id = tindakan_id.value
				rekam_medis.nama_tindakan = nama_tindakan.value

				rekam_medis.kode_icdx = kode_icdx.value
				rekam_medis.nama_icdx = nama_icdx.value

				rekam_medis.beautician_id = beautician_id.value
				rekam_medis.nama_beautician = nama_beautician.value
				
				axios.post(`api/rekam_medis/create`, rekam_medis)
				.then((response) => {
					Swal.fire({
					  title: 'Berhasil!',
					  text: 'Berhasil menambahkan data',
					  icon: 'success',
					  confirmButtonText: 'Ok'
					})
					console.log(response)
					loading.value = false
					router.replace('/perawatan')
				})
				.catch((error) => {
					Swal.fire({
					  title: 'Gagal!',
					  text: 'Gagal menambahkan data',
					  icon: 'error',
					  confirmButtonText: 'Ok'
					})
					loading.value = false
					console.log(error)
				})
			}
			// End Proses Rekam Medis
			


			onMounted(() => {
				getRekamMedis()
				getResep()
				getTindakan()
				getIcdx()
				getBeautician()
			})

			return { 
				kunjungan_id, nomor_rekam_medis, rekam_medis, prosesRekamMedis, pasien_id, nama_pasien, jenis_kelamin, umur, get_age, jenis_kelamin_pada_ui, getTindakan, tindakan, getResep, resep, resepObatModel, tambahResepKeRekamMedis, resepResult, hapusDataResep, tindakanModel, tindakanResult, tambahTindakanKeRekamMedis, hapusDataTindakan, loading, icdx, icdxModel, icdxResult, tambahIcdxKeRekamMedis, hapusDataIcdx, beautician, tambahbeauticianKeRekamMedis, id_tindakan_ke_beautician, beauticianModel, getBeautician, beauticianResult, hapusDataBeautician, resetInputBeautician
			}
		}
	}

	function useBeautician (tindakanResult, id_tindakan_ke_beautician) {
		const beautician = ref([])
		const beauticianModel = ref()
		const beauticianResult = ref([])
		const beautician_id = ref([])
		const nama_beautician = ref([])

		function tambahbeauticianKeRekamMedis () {

			let data = beautician.value[beauticianModel.value]
			let id_tindakan = id_tindakan_ke_beautician.value

			beauticianResult.value.push({
				'id': data.id,
				'nama': data.nama,
				'hapus' : true
			})

			tindakanResult.value[id_tindakan].beautician.push(beauticianResult.value)

			beautician_id.value.push(data.id)
			nama_beautician.value.push(data.nama)
		}

		function hapusDataBeautician (index) {
			for(let j = 0; j < beauticianResult.value.length; j++) {
				if (beauticianResult.value.[j].id == index) {
					beauticianResult.value.splice(j, 1)
					tindakanResult.value[id_tindakan_ke_beautician.value].beautician.splice(j, 1)

					console.log(tindakanResult.value)

					beautician_id.value.splice(j, 1)
					nama_beautician.value.splice(j, 1)
				}
			}

			if (beauticianResult.value.length == 0) {
				beauticianModel.value = ''
			}
		}

		function resetInputBeautician () {
			beauticianModel.value = ''
			beauticianResult.value = [] 
		}

		return {
			beautician,
			beauticianModel,
			tambahbeauticianKeRekamMedis,
			hapusDataBeautician,
			beauticianResult,
			resetInputBeautician,
			beautician_id,
			nama_beautician
		}
	}


	function useResepObat () {
		const resep = ref([])
		const resepObatModel = ref()
		const resepResult = ref([])
		const resep_id = ref([])
		const nama_resep = ref([])	

		function tambahResepKeRekamMedis (id) {
			let data = resep.value[id]

			resepResult.value.push({
				'id': id,
				'resep_id': data.id,
				'kode': data.kode,
				'nama': data.nama,
				'stok': data.stok,
				'signa': data.signa,
				'hapus' : true
			})

			resep_id.value.push(data.id)
			nama_resep.value.push(data.nama)
		}

		function hapusDataResep (index) {
			for(let j = 0; j < resepResult.value.length; j++) {
				if (resepResult.value.[j].id == index) {
					resepResult.value.splice(j, 1)

					resep_id.value.splice(j, 1)
					nama_resep.value.splice(j, 1)
				}
			}

			if (resepResult.value.length == 0) {
				resepObatModel.value = ''
			}
		}

		return {
			resep,
			resepObatModel,
			resepResult,
			resep_id,
			nama_resep,
			tambahResepKeRekamMedis,
			hapusDataResep
		}
	}


